/* Contact.css */

.contact-container {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin: 100px 20px 20px 20px;
  box-sizing: border-box;
  max-width: 100%;
  background-color: #f2fff2;
  font-weight: 600;
}

.instagram-link {
  position: absolute;
  bottom: 0;
  left: 0;
  margin-bottom: 30px;
  margin-left: 20px;
}

.email-address {
  position: absolute;
  bottom: 0;
  right: 0;
  margin-bottom: 30px;
  margin-right: 20px;
}

.email-address,
.instagram-link {
  padding: 3px;
  text-decoration: none;
  color: #101b11;
  transition: background-color 0.2s, color 0.2s;
}

.email-address a,
.instagram-link a {
  padding: 3px;
  text-decoration: none;
  color: #101b11;
  transition: background-color 0.2s, color 0.2s;
}

.email-address a:hover,
.instagram-link a:hover,
a:active {
  color: #f2fff2;
  background-color: #101b11;
}

@media only screen and (max-width: 768px) {
  .contact-container {
    flex-direction: column;
    align-items: end;
    margin-top: 130px;
  }

  .email-address,
  .instagram-link {
    padding: 0px;
    margin-top: 0px;
  }

  .instagram-link {
    position: static;
    bottom: auto;
    left: auto;
    margin-bottom: 0px;
    margin-left: 0px;
  }
  
  .email-address {
    position: static;
    bottom: auto;
    right: auto;
    margin-bottom: 15px;
    margin-right: 0px;
  }
}